<svelte:options tag="fds-coord-dialog"></svelte:options>
<script>
    import {get_current_component} from "svelte/internal"
    import {clickOutside} from "./clickOutside";

    let host = get_current_component().$$.root.host


    export let coordinates = {
        x: 0,
        y: 0,
        width: 0,
        height: 0,
        rotation: 0
    }


    function change(name, value) {
        host.dispatchEvent(new CustomEvent("change", {detail: {name: name, value: value}}))
    }

    function handleClickOutside() {
        //editcoord=false
    }


    export let editcoord

    export function refresh() {
        coordinates = coordinates
    }
</script>

<div class="show" style="display: inline-flex;">
    {#if coordinates}
        <div style="display:flex;align-items: center">
            <fds-image-editor-button
                    icon="flipHorizontal"
                    title="Flip Horizontal"
                    on:click={() => {
                change('flipHorizontal')
            }}
            />

            <fds-image-editor-button
                    icon="flipVertical"
                    title="Flip Vertical"
                    on:click={() => {
                  change('flipVertical')
            }}
            />
            <!-- svelte-ignore a11y-click-events-have-key-events -->
            <fds-image-editor-button type="button"
                                     on:click={() => { editcoord=!editcoord; change('editcoord',editcoord); }}>
                Coordinates
            </fds-image-editor-button>
        </div>
    {/if}

    {#if editcoord && coordinates }
        <div class="editcoord" use:clickOutside on:click_outside={handleClickOutside}>
            <div class="formLine">

                <div class="formLine">
                    <label for="x" class="more-label">x:</label>
                    <input value={coordinates.x}
                           on:change={(e) => {coordinates.x=e.target.value;coordinates=coordinates;change("x",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine">
                    <label for="y" class="more-label">y:</label>
                    <input value={coordinates.y}
                           on:change={(e) => { coordinates.y=e.target.value;coordinates=coordinates;change("y",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine">
                    <label for="width" class="more-label">width:</label>
                    <input value={coordinates.width}
                           on:change={(e) => {  coordinates.width=e.target.value;coordinates=coordinates;change("width",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine">
                    <label for="height" class="more-label">height:</label>
                    <input value={coordinates.height}
                           on:change={(e) => { coordinates.height=e.target.value;coordinates=coordinates;change("height",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>


                <div class="formLine">
                    <label for="rotation" class="more-label">rotation:</label>
                    <input value={(coordinates.rotation * 180.0) / Math.PI}
                           on:change={(e) => { coordinates.rotation=e.target.value;coordinates=coordinates;change("rotation", parseInt(e.target.value) * (Math.PI/180)) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>
            </div>
        </div>
    {/if}

</div>

